import { Alert, Table } from "react-bootstrap";
import DictWord from "../components/DictWord";
import dict from "../data/archive.json";

function ArchivePage() {
  return (
    <>
      <p>Not reviewed by other people (only me) </p>
      <p>
        For a new word or a better translation suggestions please email me{" "}
        <a href="mailto:naglis.suliokas@gmail.com">naglis.suliokas@gmail.com</a>{" "}
        or create Pull request on{" "}
        <a href="https://github.com/naglissul/sci-dict-lt">GitHub</a>
      </p>

      <hr />
      <Alert variant="info">
        USE CTRL+F OR "FIND IN PAGE" TO FIND YOUR WORD
      </Alert>
      <div className="table-container">
        <Table striped bordered hover>
          <tbody>
            <tr>
              <th>LT</th>
              <th>EN</th>
              <th>comments</th>
              <th>paiekos fraz</th>
            </tr>
            {dict?.map((word) => (
              <DictWord
                key={word.en + word.lt + word.comments}
                from={word.lt}
                en={word.en}
                comments={word.comments}
                lang="lt"
              />
            ))}
          </tbody>
        </Table>
      </div>

      <p>Abbreviations:</p>
      <p>
        VU Program sistemos 2021-2022 pirmasis kursas
        <li>MPS - matematika program sistemoms I/IIId,</li>
        <li>DM - diskreioji matematika,</li>
        <li>AT - algoritm teorija.</li>
      </p>
      <p>Contributors:</p>
      <li>Me (Naglis uliokas)</li>
      <li>Tomas Babelis</li>
    </>
  );
}

export default ArchivePage;
